
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

* {
		margin: 0;
		padding: 0;
}
*:focus {
		outline: none;
}
body {
		font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
		-moz-user-select: none;
		/*火狐*/
		-webkit-user-select: none;
		/*webkit浏览器*/
		-ms-user-select: none;
		/*IE10*/
		/*早期浏览器*/
		user-select: none;
}
div {
		list-style: none;
		-moz-user-select: none;
		 -ms-user-select: none;
		     user-select: none;
		-webkit-user-select: none;
}
ul,
	li {
		list-style: none;
		-moz-user-select: none;
		 -ms-user-select: none;
		     user-select: none;
		-webkit-user-select: none;
}
li {
		text-align: center;
}
em {
		font-style: normal;
}
